.btn {
  color: white;
  padding: 10px 18px;
  background: #000000;
  border-radius: 5px;
  min-width: 220px;
  border: none;
  font-size: 16px;
  border: 1px solid transparent;
  cursor: pointer;
  transition: 0.4s background ease-in;
}

.btn:hover {
  background-color: white;
  border: 1px solid black;
  color: black;
  transition: 0.3s background ease-in;
}

.outline {
  background-color: white;
  border: 1px solid black;
  color: black;
}

.outline:hover {
  background-color: black;
  border: 1px solid transparent;
  color: white;
}
